.project {
    width: 30%;
    height: 30vh;
    margin: 20px 10px;
    border: 2px solid rgb(243, 242, 242);
    border-radius: 10px 10px 0px 0px;
    overflow: hidden;
}

.project-browser {
    height: 20px;
    background-color: rgb(243, 242, 242);
    display: flex;
    align-items: center;
}

.project-circle {
    width: 6px;
    height: 6px;
    border-radius: 50%;
    background-color: white;
    margin: 3px;
}

.project-img {
    width: 100%;
}

@media screen and (max-width: 480px) {
    .project {
        width: 40%;
        height: 20vh;
    }
}