.intro {
    display: flex;
    height: 100vh;
}

.intro-left {
    flex: 1 1;
    align-items: center;
    justify-content: center;
}

.intro-right {
    flex: 1 1;
    position: relative;
}

.intro-left-wrapper {
    padding: 50px;
    height: 50%;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}

.intro-name {
    font-size: 59px;
    color: #342E37;
}

.intro-title {
    height: 50px;
    overflow: hidden;
}

.intro-description {
    color: #342E37;
    font-size: 20px;
}

.intro-title-item {
    height: 50px;
    font-size: 30px;
    font-weight: bold;
    color: #8CC7A1;
    display: flex;
    align-items: center;
}

.intro-title-wrapper {
    height: 100%;
    -webkit-animation: move 10s ease-in-out infinite alternate;
            animation: move 10s ease-in-out infinite alternate;
}

@-webkit-keyframes move {
    25% {
        -webkit-transform: translateY(-50px);
                transform: translateY(-50px);
    }

    50% {
        -webkit-transform: translateY(-100px);
                transform: translateY(-100px);

    }

    75% {
        -webkit-transform: translateY(-150px);
                transform: translateY(-150px);

    }
    100% {
        -webkit-transform: translateY(-150px);
                transform: translateY(-150px);
    }
}

@keyframes move {
    25% {
        -webkit-transform: translateY(-50px);
                transform: translateY(-50px);
    }

    50% {
        -webkit-transform: translateY(-100px);
                transform: translateY(-100px);

    }

    75% {
        -webkit-transform: translateY(-150px);
                transform: translateY(-150px);

    }
    100% {
        -webkit-transform: translateY(-150px);
                transform: translateY(-150px);
    }
}

.intro-img {
    width: 100%;
    height: 100%;
    object-fit: cover;
    position: absolute;
    z-index: 0;
}

.intro-bg {
    -webkit-clip-path: polygon(100% 0%, 100% 51%, 100% 100%, 25% 100%, 0% 50%, 25% 0%);
            clip-path: polygon(100% 0%, 100% 51%, 100% 100%, 25% 100%, 0% 50%, 25% 0%);
    background-color: #3C91E6;
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
    right: 0;
}

@media screen and (max-width: 480px) {
    .intro {
        flex-direction: column;
    }

    .intro-img {
        width: 87%;
        left: 28px;
    }

    .intro-left-wrapper {
        padding-top: 10px;
        height: 30px;
        align-items: center;
        text-align: center;
        justify-content: center;
        height: 100%;
    }

    .intro-description {
        display: none;
    }
}
.about {
    height: 50vh;
    display: flex;
    align-items: center;
}

.about-left {
    flex: 1 1;
    display: flex;
    align-items: center;
    justify-content: center;
    height: 100%;
}

.about-right {
    flex: 1 1;
}

.about-title {
    font-weight: 400px;
    text-align: center;
    color: #342E37;
    width: 100%;
}

.about-sub {
    margin: 20px 0px;
    color: #342E37;
}

.about-desc {
    font-weight: 300px;
    color: #342E37;
}

.about-skills {
    width: 100%;
}

.about-skill-item {
    margin: 20px 0px;
    text-align: center;
    color: #342E37;
    font-size: 20px;
}

.about-icon {
    width: 15px;
    height: 15px;
    margin-right: 20px;
}

@media screen and (max-width: 480px) {
    .about {
        flex-direction: column;
        text-align: center;
        margin-top: 50px;
        height: 50vh;
    }

    .about-left {
        width: 100%;
    }

    .about-right {
        padding: 20px;
        height: 30px;
    }
}
.pl {
    padding: 50px 100px;
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
}

.pl-text {
    width: 65%;
}

.pl-title {
    font-size: 50px;
    font-weight: 600;
}

.pl-desc {
    margin: 20px 0px;
}

.pl-list {
    width: 100%;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
}

@media screen and (max-width: 480px) {
    .pl {
        padding: 10px;
    }

    .pl-text {
        width: 100%;
    }

    .pl-desc {
        display: none;
    }
}
.project {
    width: 30%;
    height: 30vh;
    margin: 20px 10px;
    border: 2px solid rgb(243, 242, 242);
    border-radius: 10px 10px 0px 0px;
    overflow: hidden;
}

.project-browser {
    height: 20px;
    background-color: rgb(243, 242, 242);
    display: flex;
    align-items: center;
}

.project-circle {
    width: 6px;
    height: 6px;
    border-radius: 50%;
    background-color: white;
    margin: 3px;
}

.project-img {
    width: 100%;
}

@media screen and (max-width: 480px) {
    .project {
        width: 40%;
        height: 20vh;
    }
}
.contact {
    height: 54vh;
    position: relative;
}

.contact-bg {
    width: 20px;
    height: 100%;
    background-color: aquamarine;
    position: absolute;
}

.contact-wrapper {
    padding: 50px;
    display: flex;
}

.contact-left {
    flex: 1 1;
}

.contact-right {
    flex: 1 1;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

.contact-title {
    font-size: 60px;
    width: 80%;
    color: #342E37;
}

.contact-info-item {
    display: flex;
    align-items: center;
    margin: 50px 0px;
    font-weight: 300;
    color: #342E37;
}

.contact-icon {
    width: 30px;
    height: 30px;
    margin-right: 20px;
}

.contact-desc {
    font-size: 20px;
    font-weight: 200;
    color: #342E37;
}

form {
    margin-top: 20px;
}

input {
    width: 50%;
    height: 50px;
    border: none;
    border-bottom: 1px solid black;
    margin: 10x 0px;
    font-size: 14px;
    padding-left: 10px;
}

textarea {
    width: 100%;
    margin: 10x 0px;
    font-size: 14px;
    padding-left: 10px;
}

button {
    border: none;
    padding: 15px;
    background-color: aquamarine;
    color: white;
    font-weight: 500;
}

a {
    text-decoration: none;
    color: #342E37;
}

@media screen and (max-width: 480px) {
    .contact-wrapper {
        flex-direction: column;
        padding: 0px 50px;
    }

    .contact-title {
        font-size: 30px;
    }

    .contact-info-item {
        margin: 20px 0px;
        width: 100%;
    }

    .contact-desc {
        display: none;
    }

    form {
        margin-top: 0px;
        display: flex;
        flex-wrap: wrap;
    }

    input {
        width: 35%;
        height: 40px;
        margin: 10px;
        margin-left: 0;
    }

    button {
        margin-top: 10px;
    }
}
.toggle{
    width: 50px;
    height: 25px;
    border-radius: 20px;
    border: 1pz solid #999;
    background-color: white;
    position: fixed;
    top: 10px;
    right: 10px;
    z-index: 999;
    display: flex;
    align-items: center;
    justify-content: space-around;
}

.toggle-icon{
    width: 15px;
    height: 15px;
}

.toggle-button{
    width: 25px;
    height: 25px;
    border-radius: 50%;
    background-color: #999;
    position: absolute;
    cursor: pointer;
}
